import React from "react";
import { Animated } from "react-animated-css";

import companies from "../database/companies";
const { dataSaas, codeForGood } = companies;

class Portfolio extends React.Component {
  state = {
    companies: [dataSaas, codeForGood]
  };

  render() {
    return (
      <div id="portfolio_container" className="text-center  container">
        <h1>Portfolio</h1>
        <div className="row">
          <div className="col" />
          {this.state.companies.map((company, index) => {
            return (
              <div key={index}>
                <CompanyView company={company} />
              </div>
            );
          })}
          <div className="col" />
        </div>
      </div>
    );
  }
}

class CompanyView extends React.Component {
  constructor() {
    super();
    this.state = {
      isVisible: false,
      showModal: false
    };
  }

  toggleModal = () => {
    this.setState(prevState => {
      return {
        showModal: !prevState.showModal
      };
    });
  };

  showView = () => {
    this.setState({
      isVisible: true
    });
  };

  hideView = () => {
    this.setState({
      isVisible: false
    });
  };

  render() {
    return (
      <div>
        <Modal
          show={this.state.showModal}
          company={this.props.company}
          toggleModal={this.toggleModal}
        />
        <div
          className="col text-center company_card mb-3"
          onMouseEnter={this.showView}
          onMouseLeave={this.hideView}
        >
          <div className="animation_top">
            <Animated
              animateOnMount={false}
              animationIn="fadeInDown"
              animationOut="fadeOutUp"
              isVisible={this.state.isVisible}
            >
              <h4>{this.props.company.title}</h4>
              <h6>{this.props.company.date}</h6>
            </Animated>
          </div>
          <Animated
            isVisible={!this.state.isVisible}
            animationIn="fadeIn"
            animationOut="fadeOut"
            animateOnMount={false}
          >
            <img
              className="company_card_image"
              src={this.props.company.image}
              alt=""
            />
          </Animated>
          <div className="animation_bottom">
            <Animated
              animateOnMount={false}
              animationIn="fadeInUp"
              animationOut="fadeOutDown"
              isVisible={this.state.isVisible}
            >
              <button
                onClick={this.toggleModal}
                className="btn btn-outline-secondary"
              >
                Learn More Here
              </button>
            </Animated>
          </div>
        </div>
      </div>
    );
  }
}

const Modal = ({ show, company, toggleModal }) => {
  const showHideClassName = show
    ? "modalx display-block"
    : "modalx display-none";

  return (
    <div className={showHideClassName}>
      <section id="modal-main">
        <img className="modal_image" src={company.image} alt="" />
        <div className="text-left">
          <h4>{company.title}</h4>
          <h6>{company.name}</h6>
          <hr className="modal_hr" />
          <p>{company.description}</p>
          <div className="row">
            <div className="col text-right">
              <a
                className="btn btn-sm btn-outline-secondary mr-2"
                href={company.website}
                target="-blank"
              >
                website
              </a>
              <button
                className="btn btn-sm btn-outline-secondary"
                onClick={toggleModal}
              >
                close
              </button>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Portfolio;
